                </li>
                <li>
                  <a href="#none" class="ellipsis">    .</a>
                  <span>2019-11-10</span>
                </li>
              </ul>
            </div>
          </div>
          <div class="login-txt">
            <h2>LOGIN</h2>
            <div class="login-space">
              <fieldset title="/ ">
                <v-form>
                  <v-row class="pa-0 ma-0">
                    <v-col class="pa-0 ma-0" cols="12" sm="8" md="8">
                      <v-text-field
                        outlined
                        dense
                        hide-details
                        v-model="id"
                        placeholder="admin"
                        label=""
                      ></v-text-field>
                    </v-col>
                  </v-row>
                  <v-row class="pa-0 ma-0">
                    <v-col class="pa-0 mt-3" cols="12" sm="8" md="8">
                      <v-text-field
                        outlined
                        dense
                        hide-details
                        label=""
                        name="password"
                        v-model="password"
                        type="password"
                        placeholder="1234"
                        @keyup.enter="login"
                      ></v-text-field>
                    </v-col>
                  </v-row>
                  <v-btn
                    dark
                    class="elvation-0"
                    color="primary"
                    style="background-color:#4a367d;"
                    type="button"
                    title=""
                    v-on:click="login"
                  >
                    <span></span>
                  </v-btn>
                </v-form>
              </fieldset>
              <div class="st-checkbox checkbox01">
                <div class="ml-2">
                  <v-checkbox label=" " v-model="isSaveId"></v-checkbox>
                </div>
              </div>
            </div>
            <!-- / -->
            <div class="login-appservice">
