</template>

<script>
export default {
  name: "signin",
  data: () => ({
    id: null,
    password: null,
    isSaveId: false
  }),
  props: {},
  created() {
    this.id = localStorage.getItem("saveId");
    if (this.id !== null) this.isSaveId = true;
    localStorage.removeItem("access_token"); //   ,
  },
  methods: {
    login: async function() {
      var payload = {
        lgnId: this.id,
        mbrPwd: this.password
      };
      try {
        await this.$store.dispatch("setLogin", payload);
        this.$router.push({ path: "/home" });

        if (this.isSaveId === true) {
          localStorage.setItem("saveId", this.id);
        } else {
          localStorage.removeItem("saveId");
        }
      } catch (error) {
        this.$store.state.dialog = true;
        this.$store.state.dialogMessage = "   .";
        this.password = "";
        this.$router.push({ path: "/signin" });
      }
    }
  }
};
</script>

<style scoped>
