<template>
  <div>
    <div class="area-content pa-8">
      <div class="content-tit">
        <h2> </h2>
      </div>
      <!--  -->
      <div class="pa-0 area-content">
        <!--  -->
        <section class="box-area-search mb25">
          <!--  -->
          <div class="dis-ib st-input-bt mr10">
            <label class="blind" for="car-choice"></label>
            <input
              type="text"
              name="car-choice"
              id="car-choice"
              class="st-input-02 w140"
              placeholder=""
            />
            <a
              href="#none"
              class="btn-search"
              onclick="layer_open('layer11'); return false;"
              title=""
            >
              <span class="blind"></span>
            </a>
          </div>

          <!--  -->
          <div id class="st-select app-select02">
            <v-app>
              <v-select
                v-model="value"
                :items="searchType"
                item-text="name"
                item-value="type"
                label
                outlined
                dense
              ></v-select>
            </v-app>
          </div>

          <!--  -->
          <div class="dis-ib">
            <label class="blind" for="tx-car"></label>
            <input type="text" name id="tx-foothold2" class="st-input-02 w290" placeholder />
          </div>

          <a href="#none" class="btn-st05 btn-por" style="color:#fff;" title="">
            <span dark></span>
          </a>
        </section>
        <!-- // -->

        <!--  -->
        <section class="box-area-content">
          <!--  -->
          <div id class="st-table06 table01">
            <v-app id>
              <div>
                <v-data-table
                  v-model="selected"
                  :headers="headers"
                  :items="memeberList"
                  :single-select="false"
                  item-key="name02"
                  show-select
                  :page.sync="page"
                  :items-per-page="itemsPerPage"
                  hide-default-footer
                  class="elevation-1"
                  @page-count="pageCount = $event"
                  min-height="560px"
                >
                  <template v-slot:item.action="{ item }">
                    <v-btn
                      small
                      color="primary"
                      style="background-color:#4a367d;"
                      class="ml-2"
                      @click="editMember(item)"
                    ></v-btn>
                  </template>
                  <template v-slot:no-data> .</template>
                </v-data-table>
                <div class="text-center pt-2">
                  <v-pagination v-model="page" :length="pageCount"></v-pagination>
                </div>
              </div>
            </v-app>
            <MooveMemberView
              v-bind:memberModel="this.memberModel"
              v-bind:dialog="this.dialog"
              @close-dialog="closeDialog"
            ></MooveMemberView>

            <div class="box-stbtn">
              <a href="#none" class="btn-st01 btn-icon-delete mr-1" style="color:#fff;" title="">
                <span></span>
              </a>
              <a
                href="#none"
                class="btn-st01 btn-exceldownload mr-1"
                title=""
                style="color:#fff;"
              >
                <span> </span>
              </a>
              <a href="#none" class="btn-st01 btn-exceldownload" style="color:#fff;" title="">
                <span> </span>
              </a>
            </div>
          </div>
        </section>
      </div>
    </div>
  </div>
</template>

<script>
import MooveMemberView from "@/components/MooveMemberView.vue";
export default {
  name: "Member",
  data: () => ({
    dialog: false,
    value: null,
    innerDialog: false,
    memberModel: {},
    headers: [
      {
        text: " ",
        align: "center",
        sortable: true,
        value: "lgnId"
      },
      { text: "", value: "mbrNm", align: "center" },
      { text: "", value: "mbrMblNo", align: "center" },
      { text: "", value: "mbrEml", align: "center" },
      { text: "", value: "moniTp", align: "center" },
      {
        text: "",
        value: "action",
        align: "center",
        sortable: false
      }
    ],
    page: 1,
    searchType: [
      { name: "", type: "carNo" },
      { name: "", type: "wireNo" },
      { name: "", type: "drvrNm" }
    ],
    pageCount: 0,
    itemsPerPage: 10,
    selected: []
  }),
  components: {
    MooveMemberView: MooveMemberView
  },
  created() {
    this.$store.dispatch("setMemberListByBizNo");
  },
  computed: {
    memeber: {
      get() {
        return this.$store.getters.getMember;
      }
    },
    memeberList: {
      get() {
        return this.$store.getters.getMemberList;
      }
    },
    moniTpList: {
      get() {
        return this.$store.getters.getMoniTpList;
      },
      set() {}
    }
  },
  methods: {
    editMember(member) {
      this.dialog = true;
      this.memberModel = Object.assign({}, member);
    },
    deleteItem() {
      this.$store.dispatch("deleteMember", this.selected);
    },
    closeDialog() {
      this.dialog = false;
    },
    update() {
      this.$store.dispatch("updateMember", this.memberModel);
      this.close();
    },
    updatePassword() {
      this.innerDialog = false;
    },
    closeInner() {
      this.innerDialog = false;
    }
  }
};
</script>

<style scoped>
input {
  background-color: #fff !important;
}
</style>
