import Vue from 'vue'; // VueJS 
import App from './App.vue'; //    
import axios from 'axios'; //   : Axios
import router from './router'; //   :   
import store from './store'; //   

import 'babel-polyfill'; // ie11   
// vuetify UI library   
// , html   '<v-'     ,    .
// ) <v-app> <v-content> 
// https://vuetifyjs.com/ko/
import vuetify from './plugins/vuetify'; //     
import VueTheMask from 'vue-the-mask';
// import('./assets/css/basic_m.css');
import '@/assets/css/basic_m.css'; //  
import '@/assets/css/common.css'; //   
import '@/assets/css/datepicker_ko.css'; //   
import '@/assets/css/layout_m.css'; //   
import '@/assets/css/moove.css';

import 'vuetify/dist/vuetify.min.css'; //     

Vue.prototype.$Axios = axios; //   : Axios
Vue.config.productionTip = false;

new Vue({
  el: '#app',
  router, // URL  
  store, //  
  vuetify, //     
  VueTheMask,
  beforeCreate() {
    if (localStorage.getItem('access_token') !== null) {
      this.$store.dispatch('setMember');
    }
  },
  render: function(createElement) {
    return createElement(App);
  }
});
